.nav-title {
    font-size: 24px;
    font-weight: 700;
    display: inline-block;
    color: #f2f2f2;
    padding-left: 10px;
}

.nav {
  overflow: hidden;
  background-color: #6573c3;
}

.nav-link {
  float: left;
  color: #f2f2f2;
  background-color: #6573c3;
  text-align: center;
  padding: 24px 10px;
  text-decoration: none;
  font-size: 15px;

  /* these last two make the logout button in
  the nav bar look like another link */
  border:none;
  cursor: pointer;
  outline:0;
}

.nav-link:hover {
  background-color: #3f51b5;
}

.nav-right {
  float: right;
}
footer {
    margin-top: 80px;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 10px;
  margin-left:auto;
  margin-right: auto;
  padding: 10px;
  height: 9vh;
  width: 35%;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  border-style: solid;
  border-width: 0.1px;
  border-color: gray;
}


.scrollmenu {
  overflow: auto;
  white-space: nowrap;
  width: 100%;
  height: 500;
  overflow-y: auto;
  overflow-x: auto;
  -webkit-transform-origin: right top;
          transform-origin: right top;
}

.wrapText {
  white-space: normal;
}

.cardContent {
  height: 70px;
}

.componentBody {
  margin: 20px;
}

.bar {

  background-color: #2C557E
}

.list {
  width: 100%;
  margin-right:400px;
  margin-left: 30px;
}



body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0px;
}

.alert {
    background-color: #ff4545;
    margin: 50px;
}

form {
    background-color: #E0E2EC;
    width: 30%;
    height: 250px;
    margin: 20px auto;
    padding: 25px;
    border-radius: 2px;
}

input {
    margin: 5px;
    margin-bottom: 10px;
}

.log-in, .log-out, .register {
  background-color: #6573c3;
  border-radius: 4px;
  color: #f8f8f8;
  min-height: 50px;
  min-width: 100px;
  outline:0;
  cursor: pointer;
}

.log-in:hover, .log-out:hover, .register:hover {
  background-color: #3f51b5;
}

/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}

